import { TranslateService } from '@ngx-translate/core';
import { Component, ViewChild } from '@angular/core';
import {AlertController, ViewController,IonicPage, NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { pdfx } from '../../../providers/pdf/pdfx';
import { Auth } from '../../../providers/auth/auth';
import { NotificationProvider} from '../../../providers/notification/notification';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-biblioteca-solvencia',
  templateUrl: 'biblioteca-solvencia.html',
})
export class BibliotecaSolvenciaPage {

  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  public nit                 : any;
  novp:any;
  tiposus: any=[];
  nombre='' 
  cui:''
  idempresa:any;
  username: string = "";
  items: any=[];
  personalid: string = "";
  letterObj = {
    to: '',
    from: '',
    text: ''
  }
 
  pdfObj = null;
  isDisablednit :any;
  constructor(   public translateService: TranslateService,public alertCtrl: AlertController,public msj: NotificationProvider,public storage: Storage,public regService: Auth, public viewCtrl: ViewController,public Pdfx:  pdfx,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
     this.form = this._FB.group({
        nit        	   :  ['', Validators.compose([ Validators.required])],
        });

  }

  
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  

}); 
}
goBack() {
  this.navCtrl.pop();
}
  ionViewDidLoad() : void
  {
    
   
    

  }

  
  manageGallery2() : void
  {
    this.Pdfx.daordenpago('123','si',[])   
  }
  manageGallery() : void
  {
   
      
    this.regService.getReg(this.form.controls['nit'].value,'bibliotecas').then((data) => {
      var aa:string='';
      aa=String(data);
      var aar:any=[]
      if(aa.indexOf('status')>0)
      {
   aar=aa.split('"')
   var tota:any=[]
   tota.push({status:aar[9]});

   this.items=tota

      }
     
   //   this.Pdfx.daov(this.form.controls['nit'].value,'si',data)   
    }, (err) => {

      this.msj.showError3('Resultado','Resultado no encontrado.');
  
   
  });


  
   

   
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


}



